// main.jsApp.vuehtml#app

// vue
import Vue from 'vue'
import VueRouter from 'vue-router'
// import Vuex from 'vuex'

// 
import babelpolyfill from 'babel-polyfill'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-default/index.css'
import 'font-awesome/css/font-awesome.min.css'

// todo:normalize.css

// mock
import Mock from './mock'

// vuex-storeapp
import routes from './routes'
import store from './vuex/store'
import App from './App'
Mock.bootstrap()

// vueVueRouterVuexElementUI
// install
// install
Vue.use(ElementUI)
Vue.use(VueRouter)

// vuex
// Vue.use(Vuex)

// router
const router = new VueRouter({
  routes
})

// 
// sessionStoragelocalstoragesessionStoragelocalStorage
// sessionStoragesessioncookiesessionStorage
// session
// 
router.beforeEach((to, from, next) => {
  if (to.path == '/login') {
    sessionStorage.removeItem('user')
  }
  let user = JSON.parse(sessionStorage.getItem('user'))
  if (!user && to.path != '/login') {
    next({ path: '/login' })
  } else {
    next()
  }
})

// 
// 1. eltemplatecomponents
// 2. render$mount
// routerstorerouterstore
new Vue({
  // el: '#app',
  // template: '<App/>',
  router,
  store,
  // components: { App }
  render: h => h(App)
}).$mount('#app')



