<!--eleuiel-formel-form-itemel-inputel-checkboxel-button -->
<!-- -->
<template>
  <el-form :model="userAccount" :rules="rules" ref="userAccountForm" label-width="0px" class="login-container">
    <h3 class="title"></h3>
    <el-form-item prop="account">
      <!-- v-modelv-bindv-bindv-model -->
      <el-input type="text" v-model="userAccount.account" placeholder=""></el-input>
    </el-form-item>
    <el-form-item prop="checkPass">
      <el-input type="password" v-model="userAccount.checkPass" placeholder=""></el-input>
    </el-form-item>
    <el-form-item>
      <el-checkbox :model="checked" checked class="remember"></el-checkbox>
    </el-form-item>
    <el-form-item style="width:100%;">
      <el-button type="primary" style="width:100%;" @click="handleSubmit" :autofocus="true" :loading="logining"></el-button>
    </el-form-item>
  </el-form>
</template>

<script>
// apiapi
import { requestLogin } from '../api/api'
export default {
  data() {
    return {
      // loading
      logining: false,
      //  
      checked: true,
      // 
      userAccount: {
        account: 'admin',
        checkPass: '123456'
      },
      // v-model:model:modelmodel
      // ele ui
      rules: {
        account: [{ required: true, message: '', trigger: 'blur' }],
        checkPass: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  methods: {
    // 
    handleSubmit(ev) {
      var _this = this
      this.$refs.userAccountForm.validate(valid => {
        if (valid) {
          // loading
          this.logining = true
          // 
          var loginParams = {
            username: this.userAccount.account,
            password: this.userAccount.checkPass
          }
          requestLogin(loginParams)
            .then(data => {
              // loading
              this.logining = false
              // 
              let { msg, code, user } = data
              // 200
              if (code !== 200) {
                this.$message({
                  message: msg,
                  type: 'error'
                })
              } else {
                //  sessionStorage
                sessionStorage.setItem('user', JSON.stringify(user))
                // /table
                this.$router.push({ path: '/sys1/table' })
              }
            })
            .catch(err => {
              this.logining = false
              console.log('error submit!!')
              return false
            })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.login-container {
  border-radius: 5px;
  // 
  background-clip: padding-box;
  margin: 180px auto;
  width: 350px;
  padding: 35px 35px 15px 35px;
  background: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
  .title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }
  .remember {
    margin: 0px 0px 35px 0px;
  }
}
</style>


