<template>
  <div ref="vi-container" class="vi-container" :style="{'background-color':bgColor}" @mouseover="active" @mouseout="deactive">
    <div class="vi-relativeContainer">
        <div class="vi-absoluteContainer">
            <i :class="icon" class="vi-icon"></i>
            <span class="vi-text">{{text}}</span>
        </div>
    </div>
</div>
</template>

<script>
// +
// htmlcontainer
// divrelativeabsolute
export default {
  name: "vaIconbutton",
  props: {
    icon: String,
    text: String,
    color: String,
    bgColor:String,
    activeColor:String,
  },
  // mouseovermouseenterpropsactiveColorbgColor
  methods: {
      active () {
          this.$refs['vi-container'].style.backgroundColor =  this.activeColor;
      },
      deactive () {
          this.$refs['vi-container'].style.backgroundColor =  this.bgColor;
      }
  }
};
</script>

<style scoped lang="scss">
    
    // 
    .vi-container {
        height:60px;
        width:120px;
    }

    // relative
    .vi-relativeContainer {
        position:relative;
        height:100%;
    }

    // absolute
    // div
    .vi-absoluteContainer {
        width:100%;
        text-align:center;
        position:absolute;
        top:50%;
        left:50%;
        transform:translate(-50%,-50%);
    }

    // 
    .vi-icon {
        display:block;
        font-size:18px;
    }

    // 
    .vi-text {
        display:block;
        margin-top:2px;
        line-height:1;
    }
</style>



