import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
import * as getters from './getters'

// main.jsvuex
Vue.use(Vuex)

// 
const state = {
    count: 10,
    collapsed:false,
}

//  mutations
const mutations = {
    INCREMENT(state) {
        state.count++
    },
    DECREMENT(state) {
        state.count--
    },
    toogleCollapsed(state) {
        state.collapsed = !state.collapsed
    }
}

//  store 
export default new Vuex.Store({
    actions,
    getters,
    state,
    mutations
})


